/* ########################## */
/*   PAGE TITLE, ETC.         */
/* ########################## */


.page-title {
	font-family: Audiowide, Arial, Verdana, sans-serif;
	font-size: 2.0em;
	letter-spacing: 1px;
	color: WHITE;

	border-bottom: 1px solid #333;
	padding-bottom: 2px;
	margin-bottom: 25px;
	clear: both;
	text-shadow: 1px 1px 1px #000;

	a {
		color: WHITE;

		&:hover {
			text-decoration: none;
			color: #DFFF80;
		}
	}
}

.page-subheader {
	@extend .page-title;

	font-size: 1.5em;
	text-align: left;
}
